import { initAccordion } from '../../objects/accordion/accordion';
import { CLASS_ACTIVE, CLASS_INITIALISED, CLASS_VISIBLE } from '../../tools/constants/classes';

// Selectors
const SELECTOR_CONTAINER = '[data-stockists]';
const SELECTOR_ACCORDION = '[data-accordion-container]';
const SELECTOR_SLIDE = '[data-accordion-slide]';
const SELECTOR_TITLE = '[data-accordion-title]';
const SELECTOR_ACTIVE_BLOB = '[data-active-tab-blob]';
const SELECTOR_TAB = '[data-stockists-tab]';
const SELECTOR_STORE_TAB = '[data-stockists-tab-store]';
const SELECTOR_ONLINE_TAB = '[data-stockists-tab-online]';
const SELECTOR_ONLINE_WRAPPER = '[data-stockists-online-wrapper]';
const SELECTOR_STORE_WRAPPER = '[data-stockists-store-wrapper]';
const SELECTOR_COUNTRIES = '[data-stockists-countries-categories]';
const SELECTOR_COUNTRY_TAB = '[data-country-category]';
const SELECTOR_ONLINE_COUNTRY_TAB = '[data-online-country-category]';
const SELECTOR_COUNTRY_WRAPPER = '[data-stockists-country-wrapper]';
const SELECTOR_ONLINE_COUNTRY_WRAPPER = '[data-stockists-online-country-wrapper]';

// Attributes
const ATTR_COUNTRY_HANDLE = 'data-country-handle'
const ATTR_ONLINE_COUNTRY_HANDLE = 'data-online-country-handle'

// Classes
const CLASS_SECOND_ACTIVE = 'is-second-active'



/**
 * Add click listener to country tabs,
 * then displays the country wrapper whose handle matches
 * the clicked tab
 * @param countryCategories - Array of country tab elements
 * @param countryWrappers - Array of country wrapper elements
 * @param handleAttribute - String of the country handle attribute
 */
const hideShowCountryWrapper = (
  countryCategories: HTMLElement[], 
  countryWrappers: HTMLElement[],
  handleAttribute: string) => {

  // For every country tab
  countryCategories.forEach(countryCategory => {
    let countryHAndle = countryCategory.getAttribute(handleAttribute);

    // When clicked
    countryCategory.addEventListener('click', () => {

      // For every country wrapper
      countryWrappers.forEach(countryWrapper => {

        // Hide all countries wrappers
        countryWrapper.classList.remove(CLASS_VISIBLE);
        let countryWrapperHandle = countryWrapper.getAttribute(handleAttribute);

        // Show country wrapper whose handle matches the clicked country tab
        if (countryHAndle === countryWrapperHandle) countryWrapper.classList.add(CLASS_VISIBLE);
      });

      // Hide active state on all country tabs
      countryCategories.forEach(category => {
        category.classList.remove(CLASS_ACTIVE);
      });

      // Add active to clicked country tab
      countryCategory.classList.add(CLASS_ACTIVE);
    });
  }); 
}

document.querySelectorAll<HTMLElement>(SELECTOR_CONTAINER).forEach(containerElement => {
  // Check if section has already been initialized
  if (containerElement.classList.contains(CLASS_INITIALISED)) return;
  containerElement.classList.add(CLASS_INITIALISED);

  const accordion = containerElement.querySelector<HTMLDetailsElement>(SELECTOR_ACCORDION);
  const slides = containerElement.querySelectorAll<HTMLDetailsElement>(SELECTOR_SLIDE);
  const activeBlob = containerElement.querySelector<HTMLElement>(SELECTOR_ACTIVE_BLOB);
  const accordionTitles = Array.from(containerElement.querySelectorAll<HTMLElement>(SELECTOR_TITLE));
  const storeTab = containerElement.querySelector<HTMLDetailsElement>(SELECTOR_STORE_TAB);
  const onlineTab = containerElement.querySelector<HTMLDetailsElement>(SELECTOR_ONLINE_TAB);
  const storeWrapper = containerElement.querySelector<HTMLDetailsElement>(SELECTOR_STORE_WRAPPER);
  const onlineWrapper = containerElement.querySelector<HTMLDetailsElement>(SELECTOR_ONLINE_WRAPPER);
  const tab = containerElement.querySelector<HTMLDetailsElement>(SELECTOR_TAB);
  const countriesScroller = containerElement.querySelector<HTMLDetailsElement>(SELECTOR_COUNTRIES);
  const storeCountryCategories = Array.from(containerElement.querySelectorAll<HTMLElement>(SELECTOR_COUNTRY_TAB));
  const storeCountryWrappers = Array.from(containerElement.querySelectorAll<HTMLElement>(SELECTOR_COUNTRY_WRAPPER));
  const onlineCountryCategories = Array.from(containerElement.querySelectorAll<HTMLElement>(SELECTOR_ONLINE_COUNTRY_TAB));
  const onlineCountryWrappers = Array.from(containerElement.querySelectorAll<HTMLElement>(SELECTOR_ONLINE_COUNTRY_WRAPPER));


  if (!accordion ||
    !slides ||
    !accordionTitles ||
    !storeTab ||
    !onlineTab ||
    !storeWrapper ||
    !onlineWrapper ||
    !activeBlob ||
    !tab ||
    !countriesScroller ||
    !storeCountryCategories
  ) return;

  // Initialize accordion
  initAccordion({ container: accordion });


  // Tab clicks
  storeTab.addEventListener('click', () => {
    // Move active state to store tab
    storeTab.classList.add(CLASS_ACTIVE);
    onlineTab.classList.remove(CLASS_ACTIVE);
    activeBlob.classList.remove(CLASS_SECOND_ACTIVE);

    // Display in store wrapper
    onlineWrapper.classList.remove(CLASS_VISIBLE);
    storeWrapper.classList.add(CLASS_VISIBLE);
  });

  onlineTab.addEventListener('click', () => {
    // Move active state to online tab
    storeTab.classList.remove(CLASS_ACTIVE);
    onlineTab.classList.add(CLASS_ACTIVE);
    activeBlob.classList.add(CLASS_SECOND_ACTIVE);

    // Display in online wrapper
    storeWrapper.classList.remove(CLASS_VISIBLE);
    onlineWrapper.classList.add(CLASS_VISIBLE);
  });

  // Initialize listeners for country tabs
  hideShowCountryWrapper(storeCountryCategories, storeCountryWrappers, ATTR_COUNTRY_HANDLE);
  hideShowCountryWrapper(onlineCountryCategories, onlineCountryWrappers, ATTR_ONLINE_COUNTRY_HANDLE);
});